	module.exports = {"data":{"markdownRemark":{"html":"<p>Lors de la pentecôte les disciples se mirent à parler en langues et l'évangile fut prêché avec puissance. Suite à cela 3000 âmes se convertirent et reçurent le Saint-Esprit.</p>\n<blockquote>\n<p>« Pierre leur dit : Repentez-vous, et que chacun de vous soit baptisé au nom de Jésus-Christ, pour le pardon de vos péchés; <strong>et vous recevrez le don du Saint-Esprit</strong>. Ceux qui acceptèrent sa parole furent baptisés; et, en ce jour-là, le nombre des disciples s'augmenta d'environ trois mille âmes. Ils persévéraient dans l'enseignement des apôtres, dans la communion fraternelle, dans la fraction du pain, et dans les prières. » <br /> <small>Actes 2:38,41,42</small></p>\n</blockquote>\n<p>Il est intéressant de noter ici qu'il n'est indiqué nulle part que ces 3000 âmes se mirent aussi à parler en langues et la raison en est simple en réalité :</p>\n<blockquote>\n<p>« En effet, <strong>à l’un</strong> est donnée par l’Esprit une parole de sagesse; <strong>à un autre</strong>, une parole de connaissance, selon le même Esprit; <strong>à un autre</strong>, la foi, par le même Esprit; <strong>à un autre</strong>, le don des guérisons, par le même Esprit; <strong>à un autre</strong>, le don d’opérer des miracles; <strong>à un autre</strong>, la prophétie; <strong>à un autre</strong>, le discernement des esprits; <strong>à un autre</strong>, la diversité des langues; <strong>à un autre</strong>, l’interprétation des langues. » <br /> <small>1 Corinthiens 12:8‭-‬10</small></p>\n</blockquote>\n<p>Dire que le parler en langues est le signe de la nouvelle naissance signifierait que tous les chrétiens devraient parler en langues. Or, la parole de Dieu est très claire ici. Dans cette liste non exhaustive des dons de l'Esprit, Paul explique que la distribution des dons se fait selon le bon plaisir de l'Esprit-Saint et que tous n'ont pas le ou les mêmes dons !</p>","frontmatter":{"title":"Le parler en langues est-il la preuve que l'on a reçu le Saint-Esprit et qu'on est né de nouveau ?"}}},"pathContext":{"slug":"/le-parler-en-langue-est-il-la-preuve-que-l-on-a-recu-le-Saint-Esprit-et-qu-on-est-ne-de-nouveau/"}}

